.container-flex {
    max-width: 800px;
    display: flex;
    justify-content: space-between;
  }
  .container-flex .item {
    width: 100%;
    display: flex;
    justify-content: center;
    text-align: center;
  }
  .x-auto {
    margin: 0 auto;
  }
  .wr-select-form {
    display: flex;
    justify-content: space-between;
    max-width: 800px;
  }
  .wr-select-form select {
    display: block;
    width: 70%;
    height: 30px;
  }
  
  #wr-job-application {
    display: flex;
    height: 300px;
    flex-direction: column;
    justify-content: space-evenly;
    max-width: 800px;
    margin: 0 auto;
  }
  #wr-job-application input,
  #wr-job-application select,
  #wr-job-application label {
    display: block;
    width: 100%;
    max-width: 100%;
    height: 30px;
  }
  #wr-job-application button {
    margin-top: 20px;
  }